body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
    height: 100%;
}

html {
    display: table;
    margin: auto;
}

body {
  display: table-cell;
  vertical-align: middle;
  background-color: darkslategray;
}

.Home {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.Home-button {
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  background-color: red;
  color: navajowhite;
  padding: 20px 30px;
}

.App-header {
  text-align: center;
  color: navajowhite;
}

.Player-list-box-div {
  display: -webkit-flex;
  display: flex;
  float: left;
  margin: 1%;
}

.Player-list-box {
  background-color: lightblue;
}

.Player-button {;
  display: -webkit-flex;;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 1%;
  float: left;
}

.Add-button {;
  width: 80px;
  background-color: green;
  margin-bottom: 5%;
}

.Remove-button {;
  width: 80px;
  background-color: red;
  margin-bottom: 5%;
}

.Clear-button {;
  width: 80px;
  background-color: yellow;
  margin-bottom: 50%;
}

.Swap-button {;
  width: 80px;
  background-color: whitesmoke;
  margin-bottom: 5%;
}

.User-list-box-div {
  display: -webkit-flex;
  display: flex;
  float: left;
  margin: 1%;
}

.User-list-box {
  background-color: lightblue;
}

.Draft-button-div {;
  float: left;
  margin: 1%;
}

.Draft-button {;
  width: 80px;
  background-color: orange;
}

.Draft-results-div {;
  display: -webkit-flex;;
  display: flex;
  float: left;
  margin: 1%;
}

.Slider-div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  float: left;
  margin-left: 2%;
  text-align: center;
  color: navajowhite;
}

.Loading-text {
  font-size: x-large;
  font-weight: bold;
  font-style: oblique;
  text-align: center;
  color: navajowhite;
  margin-bottom: 30%;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 5s linear;
          animation: App-logo-spin infinite 5s linear;
  height: 30vmin;
  pointer-events: none;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

